import React, { Component } from 'react'

interface Iprops {
  email?: string
  passwords?: string
  message?: string
}
interface Istate {
  email?: string
  passwords?: string
  message?: string
}
export default class Login extends React.Component<Iprops, Istate> {
  constructor(props: Iprops) {
    super(props)
    this.state = { email: '', passwords: '', message: '' }
  }
  render() {
    return (
      <div className="col-lg-4 m-4 border p-3">
        <h4 className="m-2 p-2 border-bottom">Login</h4>
        <div className="form-group form-row mb-3 ">
          <label className="col-lg-4 form-label" htmlFor="email">
            Email:
          </label>
          <input
            type="text"
            className="form-control "
            id="email"
            value={this.state.email}
            onChange={(event) => {
              this.setState({ email: event.target.value })
            }}
          />
        </div>

        <div className="form-group form-row mb-3">
          <label className="col-lg-4 form-label" htmlFor="passwords">
            Password:
          </label>
          <input
            type="password"
            id="passwords"
            className="form-control"
            value={this.state.passwords}
            onChange={(event) => {
              this.setState({ passwords: event.target.value })
            }}
          />
        </div>

        <div>
          <span className="text-danger">{this.state.message}</span>
          <button className="btn btn-primary m-1 right" onClick={this.onLoginClick}>
            Login
          </button>
        </div>
      </div>
    )
  }
  // render end
  onLoginClick = async () => {
    let response = await fetch(
      `http://localhost:5000/users?email=${this.state.email}& password = ${this.state.passwords}`
    )
    let data = await response.json()
    if (data.length > 0) {
      this.setState({ message: '输入正确' })
    } else {
      this.setState({ message: '账号密码输入错误' })
    }
  }
  //事件函数结束
  //模拟服务器：Json Server
}
